<template>
    <div class="bk-nginx-server">
        <!-- 表单 -->
        <div class="bk-nginx-form">
            <div class="bk-nginx-info">
                <!-- 配置安装包来源 -->
                <div class="bk-package">
                    <div class="bk-package-head">
                        <span>配置安装包来源</span>
                    </div>
                    <div class="bk-package-form">
                        <form class="bk-form">
                            <div class="bk-form-item is-required">
                                <label class="bk-label">内网URL：</label>
                                <div class="bk-form-content" style="margin-left: 130px;">
                                    <input type="text" 
                                        style="width: 100%" 
                                        class="bk-form-input" 
                                        v-model.trim="formInfo.innerIp" 
                                        placeholder="请输入内网URL">
                                </div>
                            </div>
                            <div class="bk-form-item is-required">
                                <label class="bk-label">外网URL：</label>
                                <div class="bk-form-content" style="margin-left: 130px;">
                                    <input type="text" 
                                        style="width: 100%" 
                                        class="bk-form-input" 
                                        v-model.trim="formInfo.outerIp" 
                                        placeholder="请输入外网URL">
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
                <!-- 任务安装项目 -->
                <div class="bk-package bk-ml20">
                    <div class="bk-package-head">
                        <span>任务安装项目</span>
                    </div>
                    <div class="bk-package-form">
                        <form class="bk-form">
                            <div class="bk-form-item is-required">
                                <label class="bk-label">超时时间：</label>
                                <div class="bk-form-content" style="margin-left: 130px;">
                                    <input type="text" 
                                        style="width: 100%" 
                                        class="bk-form-input" 
                                        v-model.trim="formInfo.timeOut" 
                                        placeholder="请输入超时时间">
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
                <!-- GSE Server信息 -->
                <div class="bk-package bk-ml20">
                    <div class="bk-package-head">
                        <span>GSE Server信息</span>
                    </div>
                    <div class="bk-package-form">
                        <form class="bk-form">
                            <div class="bk-form-item">
                                <label class="bk-label">GSE内网IP：</label>
                                <div class="bk-form-content" style="margin-left: 130px;">
                                    <input type="text" 
                                        style="width: 100%" 
                                        class="bk-form-input" 
                                        v-model.trim="formInfo.gseInnerIp" 
                                        placeholder="请输入GSE内网IP">
                                </div>
                            </div>
                            <div class="bk-form-item">
                                <label class="bk-label">GSE外网IP：</label>
                                <div class="bk-form-content" style="margin-left: 130px;">
                                    <input type="text" 
                                        style="width: 100%" 
                                        class="bk-form-input" 
                                        v-model.trim="formInfo.gseOuterIp" 
                                        placeholder="请输入GSE外网IP">
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
                <!-- 保存 -->
                <div class="bk-form-btn">
                    <bk-button type="primary" title="保存">
                        保存
                    </bk-button>
                </div>
            </div>
            <div class="bk-nginx-config">
                <!-- 插件管理相关信息 -->
                <div class="bk-package bk-ml20">
                    <div class="bk-package-head">
                        <span>插件管理相关信息</span>
                    </div>
                    <div class="bk-package-form">
                        <p class="bk-package-p">最后一次配置时间：2018-11-16 16:36:46</p>
                    </div>
                </div>
                <!-- 加载插件基础信息 -->
                <div class="bk-form-btn">
                    <bk-button type="primary" title="加载插件基础信息">
                        加载插件基础信息
                    </bk-button>
                </div>
            </div>
        </div>
        <!-- 注意事项 -->
        <div class="bk-attention">
            <!-- 注意事项 -->
            <div class="bk-attention-info">
                <div class="bk-attention-head">注意事项：</div>
                <div class="bk-attention-package">
                    <p class="bk-attention-from">配置安装包来源</p>
                    <p>请填写部署蓝鲸服务器中的 nginx 所在的 IP 地址及端口. Agent 的安装包将从该 nginx 上下载. 
                        <p>1. 直连区域通过 nginx 内网 IP下载安装包 </p>
                        <p>2. 非直连区域(云区域) 通过 nginx 外网 IP 下载, 并保存在 Proxy 节点上</p>
                    </p>
                </div>
                <div class="bk-attention-package">
                    <p class="bk-attention-from">任务安装项目</p>
                    <p>安装Proxy时，需要从Nginx所在机器下载Linux、Windows、AIX所需要的agent安装包，约150 - 180 M大小, 若Proxy到Nginx之间的网络带宽较小，容易超时。可以通过增大超时时间进行设置。</p>
                </div>
                <div class="bk-attention-package">
                    <p class="bk-attention-from">GSE Sever信息</p>
                    <p>Agent/Proxy 启动后，会尝试连接GSE, 直连区域通常内网IP, 非直连区域使用外网IP，这里的内外网不是严格意义上的私有地址或公网地址，只要能让agent连上的IP都可以。通常在NAT网络环境中需要注意使用NAT 地址。</p>
                </div>
            </div>
            <!-- 插件管理信息 -->
            <div class="bk-attention-gse">
                <div class="bk-attention-package">
                    <p class="bk-attention-from">插件管理相关信息</p>
                    <p>gse 插件在打包时(安装gse时），节点管理还未安装，因此插件的基本信息(插件描述信息，包信息，进程管理信息）还未录入到节点管理中，点击加载按钮，加载插件基础信息。通常该操作仅需要在首次使用节点管理时执行。</p>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        data () {
            return {
                formInfo: {
                    innerIp: '',
                    outerIp: '',
                    timeOut: '',
                    gseInnerIp: '',
                    gseOuterIp: ''
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import '../../scss/mixins/clearfix.scss';
    @import '../../scss/mixins/scroller.scss';

    .bk-nginx-server {
        margin: 0 140px;
        height: 100%;
        overflow: auto;
        @include scroller(#fff);
        border: 1px solid #dddedf;
        background-color: #fff;
    }

    /* 表单 */
    .bk-nginx-form {
        float: left;
        width: 60%;
        height: 100%;
        border-right: 1px solid #dddedf;
    }
    .bk-nginx-info {
        height: 500px;
        padding: 27px 30px;
        border-bottom: 1px solid #dddedf;
    }
    .bk-nginx-config {
        padding: 27px 30px;
        .bk-package-p {
            font-size:14px;
            color: #737987;
            padding-left: 13px;
        }
    }
    .bk-package {
        font-size: 16px;
        color: #313b4c;
        line-height: 21px;
        .bk-package-head {
            padding-left: 13px;
            border-left: 3px solid #3c96ff;
        }
    }
    .bk-package-form {
        margin-top: 20px;
    }
    .bk-label {
        line-height: 16px;
        font-size: 14px;
        color: #737987;
        font-weight: normal;
        width: 130px;
    }
    .bk-ml20 {
        margin-top: 20px;
    }
    /* 保存 */
    .bk-form-btn {
        margin: 20px 0 0 130px;
        .bk-button {
            width: 140px;
            height: 36px;
            line-height: 36px;
            font-size: 14px;
        }
    }

    /* 注意事项 */
    .bk-attention {
        float: right;
        width: 40%;
        height: 100%;
        background-color: #fafbfd;
    }
    .bk-attention-info,
    .bk-attention-gse {
        padding: 32px 30px;
        font-size: 12px;
        color: #737987;
        line-height: 24px;
    }
    .bk-attention-info {
        height: 500px;
        border-bottom: 1px solid #dddedf;
    }
    .bk-attention-head,
    .bk-attention-from {
        font-size: 14px;
        color: #313b4c;
        line-height: 20px;
    }
    .bk-attention-from {
        font-size: 12px;
        margin-top: 8px;
    }
    .bk-attention-package {
        margin-bottom: 30px;   
    }
</style>


