<template>
    <div class="bk-advanced-set">
        <h1 class="bk-set-h1">高级添加Agent</h1>
        <div class="bk-advanced-info">
            <!-- 添加节点 -->
            <div class="bk-advanced-add">
                <ul>
                    <li>
                        <div class="bk-add-head">添加新节点</div>
                        <div class="bk-add-form">
                            <form class="bk-form">
                                <div class="bk-form-item is-required">
                                    <label class="bk-label">节点类型：</label>
                                    <div class="bk-form-content" style="margin-left: 120px;">
                                        <bk-selector
                                            class="bk-style-width"
                                            :list="nodeTypeList"
                                            :selected.sync="formInfo.type"
                                            @item-selected="selectedType">
                                        </bk-selector>
                                    </div>
                                </div>
                                <div class="bk-form-item">
                                    <label class="bk-label">登录IP：</label>
                                    <div class="bk-form-content" style="margin-left: 120px;">
                                        <input type="text" 
                                            class="bk-form-input bk-style-width" 
                                            v-model.trim="formInfo.loginIp" 
                                            placeholder="填写从APPO登录时，所使用的目标机器IP">
                                        <div class="bk-cmdb">
                                            <label class="bk-form-checkbox bk-checkbox-small">
                                                <input type="checkbox" name="checkLogin">
                                                <i class="bk-checkbox-text">注册此IP到CMDB中</i>
                                            </label>
                                        </div>
                                    </div>
                                </div>
                                <div class="bk-form-item">
                                    <label class="bk-label">通信IP：</label>
                                    <div class="bk-form-content" style="margin-left: 120px;">
                                        <input type="text" 
                                            class="bk-form-input bk-style-width" 
                                            v-model.trim="formInfo.communicationIp" 
                                            placeholder="启动Agent后，用于与Server端建立连接的本机网卡IP">
                                        <div class="bk-cmdb">
                                            <label class="bk-form-checkbox bk-checkbox-small">
                                                <input type="checkbox" name="checkCommunication">
                                                <i class="bk-checkbox-text">注册此IP到CMDB中</i>
                                            </label>
                                        </div>
                                    </div>
                                </div>
                                <div class="bk-form-item">
                                    <label class="bk-label">数据IP：</label>
                                    <div class="bk-form-content" style="margin-left: 120px;">
                                        <input type="text" 
                                            class="bk-form-input bk-style-width" 
                                            v-model.trim="formInfo.dataIp" 
                                            placeholder="上报数据绑定的IP，将被注册到CMDB中">
                                        <div class="bk-cmdb">
                                            <label class="bk-form-checkbox bk-checkbox-small">
                                                <input type="checkbox" name="checkData">
                                                <i class="bk-checkbox-text">注册此IP到CMDB中</i>
                                            </label>
                                        </div>
                                    </div>
                                </div>
                                <div class="bk-form-item">
                                    <label class="bk-label">级联IP：</label>
                                    <div class="bk-form-content" style="margin-left: 120px;">
                                        <input type="text" 
                                            class="bk-form-input bk-style-width" 
                                            v-model.trim="formInfo.cascadeIp" 
                                            placeholder="被p-agent连接的proxy IP">
                                    </div>
                                </div>
                                <div class="bk-form-item is-required">
                                    <label class="bk-label">操作系统：</label>
                                    <div class="bk-form-content" style="margin-left: 120px;">
                                        <bk-selector
                                            class="bk-style-width"
                                            :list="operatList"
                                            :selected.sync="formInfo.operat"
                                            @item-selected="selectedOperat">
                                        </bk-selector>
                                    </div>
                                </div>
                                <div class="bk-form-item">
                                    <label class="bk-label">登录端口：</label>
                                    <div class="bk-form-content" style="margin-left: 120px;">
                                        <input type="text" 
                                            class="bk-form-input bk-style-width" 
                                            v-model.trim="formInfo.port" 
                                            placeholder="请输入登录端口">
                                    </div>
                                </div>
                                <div class="bk-form-item">
                                    <label class="bk-label">登录账号：</label>
                                    <div class="bk-form-content" style="margin-left: 120px;">
                                        <input type="text" 
                                            class="bk-form-input bk-style-width" 
                                            v-model.trim="formInfo.account" 
                                            placeholder="请输入登录账号">
                                    </div>
                                </div>
                                <div class="bk-form-item">
                                    <label class="bk-label">认证方式：</label>
                                    <div class="bk-form-content" style="margin-left: 120px;">
                                        <bk-selector
                                            class="bk-style-width"
                                            :list="authenticationList"
                                            :selected.sync="formInfo.method"
                                            @item-selected="selectedMethod">
                                        </bk-selector>
                                        <div class="bk-cmdb">
                                            <label class="bk-form-checkbox bk-checkbox-small">
                                                <input type="checkbox" name="checkMethod">
                                                <i class="bk-checkbox-text">加密保存认证信息</i>
                                            </label>
                                        </div>
                                    </div>
                                </div>
                                <div class="bk-form-item">
                                    <label class="bk-label">认证资料：</label>
                                    <div class="bk-form-content" style="margin-left: 120px;">
                                        <bk-button type="default" title="点击上传密钥文件" class="bk-file-btn">
                                            <i class="bk-icon icon-save" style="margin-right: 10px;"></i>点击上传密钥文件
                                            <input type="file" class="bk-input-file">
                                        </bk-button>
                                        <!-- <div class="bk-">
                                            
                                        </div> -->
                                    </div>
                                    <div class="bk-form-content" style="margin-left: 120px; margin-top: 20px;">
                                        <bk-button type="default" title="点击上传证书文件" class="bk-file-btn">
                                            <i class="bk-icon icon-save" style="margin-right: 10px;"></i>点击上传证书文件
                                            <input type="file" class="bk-input-file">
                                        </bk-button>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </li>
                    <li>
                        <div class="bk-add-head">添加新节点</div>
                        <div class="bk-add-form">
                            
                        </div>
                    </li>
                </ul>
            </div>
            <!-- 注意事项 -->
            <div class="bk-advanced-attention">
                <div class="bk-attention-head">注意事项：</div>
                <div class="bk-attention-package">
                    <p>Agent 服务器需要满足以下条件：</p> 
                    <p>1. APPO 所在机器(在 app 运行所在机器) 必须能通过 ssh 登陆到 Agent 机器 </p>
                    <p>2. Agent 所在机器可以访问到 zk 的端口</p> 
                    <p>3. 支持 Linux/Windows/AIX 操作系统</p> 
                    <p>4. Windows 服务器如果没有安装 Cygwin， 则需要开通139, 445端口 </p>
                    <p>5. 必须使用 root/Administrator 账户</p>
                </div>
            </div>
        </div>
        <div class="bk-advanced-btn">
            <bk-button type="primary" title="开始安装">
                开始安装
            </bk-button>
            <bk-button type="default" title="仅导入机器">
                仅导入机器
            </bk-button>
            <bk-button type="default" title="取消">
                取消
            </bk-button>
        </div>
    </div>
</template>
<script>
    export default {
        data () {
            return {
                formInfo: {
                    type: 'Agent',
                    loginIp: '',
                    communicationIp: '',
                    dataIp: '',
                    cascadeIp: '',
                    operat: 'Linux',
                    port: '',
                    account: '',
                    method: 1
                },
                // 节点类型
                nodeTypeList: [
                    {id: 'Agent', name: 'Agent'},
                    {id: 'Proxy', name: 'Proxy'}
                ],
                // 操作系统
                operatList: [
                    {id: 'Linux', name: 'Linux'},
                    {id: 'Windows', name: 'Windows'},
                    {id: 'AIX', name: 'AIX'}
                ],
                // 认证方式
                authenticationList: [
                    {id: 1, name: '密钥+证书认证'},
                    {id: 2, name: '密钥认证'},
                    {id: 3, name: '证书认证'}
                ]
            }
        },
        methods: {
            // 节点类型
            selectedType (id, data) {
                this.formInfo.type = id
            },
            // 操作系统
            selectedOperat (id, data) {
                this.formInfo.operat = id
            },
            // 认证方式
            selectedMethod (id, data) {
                this.formInfo.method = id
            }
        }
    }
</script>
<style lang="scss" scoped>
    @import '../../scss/mixins/clearfix.scss';
    @import '../../scss/mixins/scroller.scss';

    .bk-advanced-set {
        padding: 30px 140px;
        height: 100%;
    }
    .bk-set-h1 {
        text-align: center;
        font-size: 24px;
        color: #333C48;
        line-height: 32px;
        font-weight: normal;
        margin: 0 0 30px 0;
    }
    .bk-advanced-info {
        width: 100%;
        height: calc(100% - 84px);
        background-color: #fafbfd;
        border: 1px solid #dddedf;
        border-bottom: 0;
    }

    /* 添加节点 */
    .bk-advanced-add {
        width: 65%;
        float: left;
        background-color: #fff;
        height: 100%;
        overflow: auto;
        @include scroller(#fff);
        li {
            font-size: 14px;
            background-color: #fafbfd;
        }
        .bk-add-head {
            padding-left: 30px;
            cursor: pointer;
            color: #313B4C;
            line-height: 40px;
            border-bottom: 1px solid #dddedf;
        }
        .bk-add-form {
            width: 100%;
            padding: 30px 30px 30px 0;
            border-bottom: 1px solid #dddedf;
            background-color: #fff;
            .bk-form-item {
                @include clearfix;
            }
            .bk-label {
                width: 120px;
                font-weight: normal;
                height: 36px;
                line-height: 18px;
            }
            .bk-style-width {
                width: 70%;
                float: left;
            }
            .bk-cmdb {
                width: 30%; 
                float: right; 
                height: 36px; 
                padding-left: 20px;
                .bk-checkbox-text {
                    color: #737987;
                    font-size: 14px;
                }
            }
            .bk-button {
                height: 42px;
                font-size: 14px;
            }
            .bk-file-btn {
                width: 70%;
                overflow: hidden;
                position: relative;
            }
            .bk-input-file {
                width: 100%;
                height: 100%;
                position: absolute;
                top: 0;
                left: 0;
                opacity: 0;
                cursor: pointer;
            }
        }
    }

    /* 注意事项 */
    .bk-advanced-attention {
        width: 35%;
        height: 100%;
        padding: 24px 30px;
        border-left: 1px solid #dddedf;
        float: right;
        font-size: 12px;
        color: #737987;
        line-height: 24px;
        .bk-attention-head {
            font-size: 14px;
            color: #313b4c;
            margin-bottom: 11px;
        }
    }

    /* 底部按钮 */
    .bk-advanced-btn {
        position: fixed;
        bottom: 0;
        left: 0;
        width: 100%;
        text-align: center;
        padding: 5px 0;
        box-shadow: 1px 2px 10px rgba(0,0,0,0.5);
        .bk-button {
            height: 42px;
            width: 140px;
            font-size: 14px;
        }
    }
</style>


